<template>
  <div class="box">
    <van-tabs>
      <van-tab title="全部">
        <div class="item" v-for="(item, index) in data0" :key="index">
          <div class="top">
            <span>申请时间：{{item.create_time}}</span>
            <span v-if="item.status==-1"  class="bg1">未通过</span>
            <span v-if="item.status==0" class="bg2">审核中</span>
            <span v-if="item.status==1" class="bg3">已完成</span>
          </div>
          <div class="bottom">
            <img :src="item.head_pic" alt="" class="icon">
            <div class="b_r">
              <ul>
                <li>{{item.realname}}</li>
                <li>提现金额：<span class="co">{{item.money}}元</span></li>
              </ul>
              <ul>
                <li>预计到账金额</li>
                <li><span class="co">{{item.money}}元</span></li>
              </ul>
            </div>
          </div>
          <div>提现账号 ( {{item.type==0?'微信':'支付宝'}} )：{{item.bank_card}}</div>
        </div>
        <div class="no_data">{{data0.length==0?'暂无数据':'没有更多了'}}</div>
      </van-tab>
      <van-tab title="审核中">
        <div class="item" v-for="(item, index) in data1" :key="index">
          <div class="top">
            <span>申请时间：{{item.create_time}}</span>
            <span class="bg2">审核中</span>
          </div>
          <div class="bottom">
            <img :src="item.head_pic" alt="" class="icon">
            <div class="b_r">
              <ul>
                <li>{{item.realname}}</li>
                <li>提现金额：<span class="co">{{item.money}}元</span></li>
              </ul>
              <ul>
                <li>预计到账金额</li>
                <li><span class="co">{{item.money}}元</span></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="no_data">{{data1.length==0?'暂无数据':'没有更多了'}}</div>
      </van-tab>
      <van-tab title="已完成">
        <div class="item" v-for="(item, index) in data2" :key="index">
          <div class="top">
            <span>申请时间：{{item.create_time}}</span>
            <span class="bg3">已完成</span>
          </div>
          <div class="bottom">
            <img :src="item.head_pic" alt="" class="icon">
            <div class="b_r">
              <ul>
                <li>{{item.realname}}</li>
                <li>提现金额：<span class="co">{{item.money}}元</span></li>
              </ul>
              <ul>
                <li>预计到账金额</li>
                <li><span class="co">{{item.money}}元</span></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="no_data">{{data2.length==0?'暂无数据':'没有更多了'}}</div>
      </van-tab>
      <van-tab title="未通过">
        <div class="item" v-for="(item, index) in data3" :key="index">
          <div class="top">
            <span>申请时间：{{item.create_time}}</span>
            <span class="bg1">未通过</span>
          </div>
          <div class="bottom">
            <img :src="item.head_pic" alt="" class="icon">
            <div class="b_r">
              <ul>
                <li>{{item.realname}}</li>
                <li>提现金额：<span class="co">{{item.money}}元</span></li>
              </ul>
              <ul>
                <li>预计到账金额</li>
                <li><span class="co">{{item.money}}元</span></li>
              </ul>
            </div>
          </div>
        </div>
        <div class="no_data">{{data3.length==0?'暂无数据':'没有更多了'}}</div>
      </van-tab>
    </van-tabs>
  </div>
</template>

<script>
import { getwithdrawals } from '@/axios/getData'
import { Tabs, Tab } from 'vant';
export default {
  data(){
    return{
      data:[],
      active:1,
      data0:[],  // 全部
      data1:[],  // 审核中
      data2:[],  // 已完成
      data3:[],  // 未通过
    }
  },
  components: {
    vanTabs: Tabs,
    vanTab: Tab
  },
  created() {
    this.getdata();
  },
  methods:{
    async getdata(){
      var res = await getwithdrawals();
      this.data0 = res.data.list
      var list = res.data.list
      list.forEach((item, i) => {
        switch (item.status) {
          case -1:
            this.data3.push(item)
            break;
          case 0:
            this.data1.push(item)
            break;
          case 1:
            this.data2.push(item)
            break;
          default:
            break;
        }
      });
    }
  }
}
</script>

<style scoped lang="scss">
.item{
  padding:30px;
  background: #fff;
  border-bottom: 1px solid #eee;
  .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .bottom{
    display: flex;
    align-items: center;
    padding:10px 0;
    &>div{
      flex:1;
      display: flex;
      justify-content: space-between;
    }
  }
}
.icon{
  margin-right:20px;
  width:85px;
  height:85px;
  border-radius: 50%;
}
.bg1{
  color:#999;
  border:2px solid #999;
  padding:0px 10px;
  border-radius: 6px;
  font-size: 10px;
}
.bg2{
  color:rgb(26, 168, 235);
  border:2px solid rgb(26, 168, 235);
  padding:0px 10px;
  border-radius: 6px;
  font-size: 10px;
}
.bg3{
  color:rgb(106, 247, 63);
  border:2px solid rgb(106, 247, 63);
  padding:0px 10px;
  border-radius: 6px;
  font-size: 10px;
}
.co{
  color:red;
  font-weight: 600;
}
.no_data{
  text-align: center;
  margin-top: 40px;
  color:#999;
}
</style>



